@import "_partial/_config";

/* background */
.ticketReserved,
.ticketSeat,
.ticketPrivilege,
.ticketCastSchedule,
.ticketIndex {
	background: url(/img/img01.jpg) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}

/* -----------------------------------------------------------------------
## index #################################################################
----------------------------------------------------------------------- */
.ticket {
	padding-top: 16px;
	.calenderWrapper {
		margin-top: 16px;
		position: relative;
		text-align: center;
		.sp {
			display: none;
		}
	}
	.legend {
		text-align: center;
		li {
			display: inline-block;
		}
	}
	.nowon {
		display: block;
		margin-top: 16px;
		padding: 1em;
		text-align: center;
		border: 2px solid $color_line;
		transition: all .4s ease;
		text-align: center;
		cursor: pointer;
		&:hover {
			background: $color_bg_light;
			color: $color_text_dark;
		}
	}
	.boxTitle {
		margin-top: 32px;
		text-align: center;
	}
	.ticket_prereserve,
	.ticket_theDay {
		.col1Box,
		.col2Box,
		.col3Box {
			margin-top: 16px;
		}
		.col1Box_item_title,
		.col2Box_item_title,
		.col3Box_item_title {
			margin-top: 4px;
			text-align: center;
		}
		.col1Box_item_tel,
		.col2Box_item_tel,
		.col3Box_item_tel {
			margin-top: 4px;
			text-align: center;
		}
		.col1Box_item_text,
		.col2Box_item_text,
		.col3Box_item_text {
			margin-top: 4px;
			@include fontsize(14);
		}
	}
	.ticket_theDay {
		.col1Box {
			width: 269px;
			margin-right: auto;
			margin-left: auto;
		}
	}
	.prereserve_internet {
		.boxTitle {
			line-height: 26px;
			.fa {
				border-radius: 1000px;
				margin-right: 8px;
				padding: 12px 11px;
				background: $color_bg_light;
				color: $color_text_dark;
				@include fontsize(21);
			}
		}
	}
	.prereserve_tel {
		.boxTitle {
			line-height: 26px;
			.fa {
				border-radius: 1000px;
				margin-right: 8px;
				padding: 8px 11px;
				background: $color_bg_light;
				color: $color_text_dark;
				@include fontsize(26);
			}

		}
	}
}
@include mediaQuery(sp) {
	.ticket {
		.calenderWrapper {
			margin: 0 -10px;
			.pc {
				display: none;
			}
			.sp {
				display: block;
			}
		}
		.ticket_prereserve,
		.ticket_theDay {
			.col1Box_bnr,
			.col2Box_bnr,
			.col3Box_bnr {
				img {
					width: 100%;
					height: auto;
				}
			}
		}
		.col1Box_item,
		.col2Box_item,
		.col3Box_item {
			margin-top: 16px;
		}
	}
}


/* -----------------------------------------------------------------------
## cast_schedule #########################################################
----------------------------------------------------------------------- */
.castSchedule {
	padding-top: 16px;
	&Navi {
		display: none;
		margin-top: 16px;
		&_item {
			text-align: center;
		}
		.prev {
			float: left;
			text-align: left;
		}
		.next {
			float: right;
			text-align: right;
		}
		.selectWrapper {
			display: inline-block;
			position: relative;
			border-radius: 1000px;
			background: $color_bg_light;
			&:after {
				position: absolute;
				top: 0;
				bottom: 0;
				z-index: 1;
				right: 8px;
				content: "\f078";
				display: block;
				color: $color_text_dark;
				font-family: FontAwesome;
				margin: auto;
				height: 1em;
				width: 1em;
				text-align: center;
				line-height: 1;
			}
		}
		select {
			position: relative;
			z-index: 2;
			line-height: 44px;
			padding: 0 40px 0 20px;
			color: $color_text_dark;
			cursor: pointer;
		}
	}
	.castScheduleTable {
		display: none;
		&_header {
			display: flex;
			margin-top: 32px;
			padding: 8px 0;
			border-top: 1px solid $color_line;
			.date {
				flex: 1;
				line-height: 1;
				padding: 1.3em 0;
				text-align: center;
			}
			.players {
				flex: 3;
				display: flex;
				&_item {
					flex: 1;
					padding: 0 8px;
					text-align: center;
					font-weight: bold;
				}
				.selectWrapper {
					display: block;
					position: relative;
					margin-top: 4px;
					background: $color_bg_light;
					border-radius: 1000px;
					@include fontsize(12);
					font-weight: normal;
					&:after {
						display: block;
						position: absolute;
						top: 0;
						bottom: 0;
						right: 4px;
						line-height: 1;
						height: 1em;
						margin: auto;
						content: "\f078";
						font-family: FontAwesome;
						color: $color_text_dark;
					}
				}
				select {
					width: 100%;
					padding: 8px 16px;
					text-align: center;
					color: $color_text_dark;
				}
			}
			.mime {
				color: $color_mime;
			}
			.breakin {
				color: $color_breakin;
			}
			.magic {
				color: $color_magic;
			}
			.jaggling {
				color: $color_jaggling;
			}
			.dall {
				color: $color_dall;
			}
		}
		.castScheduleList {
			table-layout: fixed;
			width: 100%;
			border-bottom: 1px solid $color_line;
			@include fontsize(14);
			th {
				font-weight: normal;
			}
			tr {
				border-top: 1px solid $color_line;
			}
			th, td {
				padding: 1em 0;
			}
			.noData[v-cloak] {
				display: none;
			}
			.noData td {
				padding: 3em 0;
				text-align: center;
			}
			.date_item {
				&.day {
					width: 81.75px;
				}
				&.weeks {
					width: 27px;
				}
				&.start {
					width: 110px;
				}
			}
			.players_item {
				position: relative;
				width: 15%;
				padding-left: 35px;
				@include fontsize(13);
				&.mime {
					background-color: $color_mime;
				}
				&.breakin {
					background-color: $color_breakin;
				}
				&.magic {
					background-color: $color_magic;
				}
				&.jaggling {
					background-color: $color_jaggling;
				}
				&.dall {
					background-color: $color_dall;
				}
				img {
					position: absolute;
					top: 0;
					bottom: 0;
					left: 1px;
					width: 30px;
					height: 30px;
					margin: auto;
				}
			}
			.suspend {
				td {
					text-align: center;
				}
			}

			&_item {
				display: flex;
				line-height: 1;
				padding: 1px 0;
				border-top: 1px solid $color_line;
				@include fontsize(14);
				.date {
					display: flex;
					flex: 1;
					&_item {
						flex: 1;
						padding: 1em 0;
						text-align: center;
					}
					.day {
						flex: 3;
					}
					.weeks {
						flex: 1;
					}
					.start {
						flex: 4;
					}
				}
				.players {
					display: flex;
					flex: 3;
					line-height: 14px;
					&_item {
						flex: 1;
						margin-left: 1px;
						padding: 1em 8px 1em 30px;
						@include fontsize(13);
					}
					.suspendText {
						flex: 1;
						padding: 1em 8px;
						text-align: center;
					}
				}
			}
		}
	}
	.resetBtn {
		width: 100%;
		margin: 32px 0 0;
		padding: 16px;
		border: 2px solid $color_line;
		cursor: pointer;
		transition: all .6s ease;
		&:hover {
			background: $color_bg_light;
			color: $color_text_dark;
		}
		&.loading {
			margin-top: 10em;
		}
	}
	.loader {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		margin: auto;
		text-align: center;
		.fa {
			font-size: 3em;
		}
		.fa-spin {
		  -webkit-animation: fa-spin 4s infinite linear;
		  animation: fa-spin 4s infinite linear;
		}
	}
}

@include mediaQuery(sp){
	.castSchedule {
		&Navi {
			.prev,
			.next {
				a {
					border: 1px solid $color_line;
				}
			}
			.selectWrapper {
				margin-top: 16px;
			}
		}
		.castScheduleTable {
			&_header {
				display: block;
				.date {
					display: none;
				}
				.players {
					display: block;
					&_item {
						flex: 0;
						float: left;
						width: 50%;
						margin-top: 8px;
						&:nth-child(even){
							float: right;
						}
					}
				}
			}
			.castScheduleList {
				display: block;
				tbody {
					display: block;
				}
				tr {
					clear: both;
					overflow: hidden;
					display: block;
					padding-bottom: 1em;
				}
				td {
					display: block;
				}
				&_item {
					display: block;
					padding-bottom: 1em;
				}
				.suspend {
					td {
						padding: 0 0 1em;
					}
				}
				.date {
					display: block;
					&_item {
						display: inline-block;
						margin-right: 8px;
						text-align: left;
					}
				}
				.players {
					display: block;
					&_item {
						float: left;
						width: 50%;
						margin-left: 0;
						padding: 1em 8px 1em 48px;
						&:nth-child(odd) {
							float: right;
						}
					}
				}
			}
		}
	}
}


/* -----------------------------------------------------------------------
## seat ##################################################################
----------------------------------------------------------------------- */
.seat {
	padding-top: 16px;
}


/* -----------------------------------------------------------------------
## privilege #############################################################
----------------------------------------------------------------------- */
.privilege {
	padding-top: 16px;
	.visualBox_text {
		line-height: 1.5;
		@include fontsize(14);
	}
}
@include mediaQuery(sp){
	.visualBox_text {
		line-height: 1.5;
		@include fontsize(12);
	}
}


/* -----------------------------------------------------------------------
## reserved ##############################################################
----------------------------------------------------------------------- */
.reserved {
	padding-top: 16px;
	.visualBox_text {
		line-height: 1.5;
		@include fontsize(14);
	}
}
@include mediaQuery(sp){
	.visualBox_text {
		line-height: 1.5;
		@include fontsize(12);
	}
}